<template>
  <v-container
    fluid
    grid-list-md>
    <core-progress-modal :show="loading" />
    <template v-if="!flgDigitalizando && !loading">
      <v-form
        ref="form2"
        @submit.prevent="validateBeforeSubmit">
        <v-layout
          row
          wrap>
          <v-flex
            sm12
            class="mb-4">
            <v-alert
              :value="true"
              type="warning">
              <span class="subheading">Anexe abaixo os documentos digitalizados do cliente, formato: JPG, JPEG ou PNG.</span>
            </v-alert>
          </v-flex>

          <v-flex
            md3
            class="mr-5">
            <v-card min-width="250">
              <v-card-title>
                <v-layout justify-space-around>
                  <span class="title">Proposta de Adesão</span>
                </v-layout>
              </v-card-title>

              <v-divider/>

              <v-layout justify-space-around>
                <v-icon
                  v-if="!docProposta"
                  size="100"
                  class="mb-4 mt-2"
                  @click="openDigitalizar('P')"
                >mdi-file-plus</v-icon>
                <v-img
                  v-else
                  :src="docProposta.strBase64"
                  @click="openDialog(docProposta.strBase64)"/>
              </v-layout>

              <v-card-actions v-if="$root.isAdmin() && docProposta">
                <v-spacer />
                <v-icon
                  color="red"
                  @click="openDialogDelete(docProposta.anexoId)">mdi-delete</v-icon>
              </v-card-actions>
            </v-card>
          </v-flex>

          <v-flex
            md3
            class="mr-5">
            <v-card min-width="250">
              <v-card-title>
                <v-layout justify-space-around>
                  <span class="title">RG (frente)</span>
                </v-layout>
              </v-card-title>

              <v-divider/>

              <v-layout justify-space-around>
                <v-icon
                  v-if="!docRgFrente"
                  size="100"
                  class="mb-4 mt-2"
                  @click="openDigitalizar('F')"
                >mdi-card-account-details</v-icon>
                <v-img
                  v-else
                  :src="docRgFrente.strBase64"
                  @click="openDialog(docRgFrente.strBase64)"/>
              </v-layout>

              <v-card-actions v-if="$root.isAdmin() && docRgFrente">
                <v-spacer />
                <v-icon
                  color="red"
                  @click="openDialogDelete(docRgFrente.anexoId)">mdi-delete</v-icon>
              </v-card-actions>
            </v-card>
          </v-flex>
		
		  <v-flex md1>
            <span class="layout fill-height align-center justify-left">OU</span>
          </v-flex>

		  <v-flex
            md3
			class="mr-5">
			<v-card min-width="250">
              <v-card-title>
                <v-layout justify-space-around>
                  <span class="title">CNH</span>
                </v-layout>
              </v-card-title>

              <v-divider/>

              <v-layout justify-space-around>
                <v-icon
                  v-if="!docCnh"
                  size="100"
                  class="mb-4 mt-2"
                  @click="openDigitalizar('C')"
                >mdi-card-plus</v-icon>
                <v-img
                  v-else
                  :src="docCnh.strBase64"
                  @click="openDialog(docCnh.strBase64)"/>
              </v-layout>

              <v-card-actions v-if="$root.isAdmin() && docCnh">
                <v-spacer />
                <v-icon
                  color="red"
                  @click="openDialogDelete(docCnh.anexoId)">mdi-delete</v-icon>
              </v-card-actions>
            </v-card>
          </v-flex>

          <v-flex
            v-for="(doc, i) in docOutros"
            :key="i"
            md3
            class="mr-5">
            <v-card min-width="250">
              <v-card-title>
                <v-layout justify-space-around>
                  <span class="title">Outros</span>
                </v-layout>
              </v-card-title>

              <v-divider/>

              <v-layout justify-space-around>
                <v-img
                  :src="doc.strBase64"
                  @click="openDialog(doc.strBase64)"/>
              </v-layout>

              <v-card-actions v-if="$root.isAdmin()">
                <v-spacer />
                <v-icon
                  color="red"
                  @click="openDialogDelete(doc.anexoId)">mdi-delete</v-icon>
              </v-card-actions>
            </v-card>
          </v-flex>

          <v-flex
            md3
            class="mr-5">
            <v-card min-width="250">
              <v-card-title>
                <v-layout justify-space-around>
                  <span class="title">Outros</span>
                </v-layout>
              </v-card-title>

              <v-divider/>

              <v-layout justify-space-around>
                <v-icon
                  size="100"
                  class="mb-4 mt-2"
                  @click="openDigitalizar('O')"
                >mdi-plus</v-icon>
              </v-layout>
            </v-card>
          </v-flex>

		  <v-flex
            md3
            class="mr-5">
            <v-card min-width="250">
              <v-card-title>
                <v-layout justify-space-around>
                  <span class="title">RG (verso)</span>
                </v-layout>
              </v-card-title>

              <v-divider/>

              <v-layout justify-space-around>
                <v-icon
                  v-if="!docRgVerso"
                  size="100"
                  class="mb-4 mt-2"
                  @click="openDigitalizar('V')"
                >mdi-card-plus</v-icon>
                <v-img
                  v-else
                  :src="docRgVerso.strBase64"
                  @click="openDialog(docRgVerso.strBase64)"/>
              </v-layout>

              <v-card-actions v-if="$root.isAdmin() && docRgVerso">
                <v-spacer />
                <v-icon
                  color="red"
                  @click="openDialogDelete(docRgVerso.anexoId)">mdi-delete</v-icon>
              </v-card-actions>
            </v-card>
          </v-flex>

          <v-flex
            md12
            class="mt-5">
            <v-layout class="justify-end">
              <v-btn
                dark
                @click="goBack">{{ editando ? 'Sair' : 'Voltar' }}</v-btn>
              <v-btn
                :loading="loadingBtn"
                type="submit"
                color="primary">{{ editando ? 'Salvar' : 'Continuar' }}</v-btn>
            </v-layout>
          </v-flex>
        </v-layout>
      </v-form>
    </template>

    <template v-if="flgDigitalizando && !loading">
      <core-digitalizar-documento
        :id="value.id"
        :loja-id="value.lojaId"
        :tipo="tipo"
        :callback="callbackDigitalizarDoc"/>
    </template>

    <template v-if="currentImage && !loading">
      <v-dialog
        v-model="dialogImage"
        width="600"
        scrollable>
        <v-card>
          <v-card-text>
            <v-img :src="currentImage"/>
          </v-card-text>
        </v-card>
      </v-dialog>
    </template>

    <template v-if="!loading">
      <v-layout
        row
        justify-center>
        <v-dialog
          v-model="dialogDelete"
          max-width="390">
          <v-card>
            <v-card-title class="headline">Excluir Documento</v-card-title>

            <v-card-text>Confirma a exclusão do documento, essa operação não poderá ser desfeita?</v-card-text>

            <v-card-actions>
              <v-spacer />

              <v-btn
                color="primary"
                @click="closeDialogDelete">Cancelar</v-btn>
              <v-btn
                :loading="loadingBtn"
                color="red"
                @click="deleteItem">Excluir</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-layout>
    </template>
  </v-container>
</template>
<script>
import { ClienteBusiness, ClienteLojaBusiness } from '../../business'

export default {
  props: {
    value: {
      type: Object,
      required: true
    },
    // eslint-disable-next-line vue/require-default-prop
    nextStep: {
      type: Function,
      requided: true
    },
    // eslint-disable-next-line vue/require-default-prop
    goBack: {
      type: Function,
      requided: true
    },
    // eslint-disable-next-line vue/require-default-prop
    setData: {
      type: Function,
      requided: true
    },
    // eslint-disable-next-line vue/require-default-prop
    disabled: false,
    // eslint-disable-next-line vue/require-default-prop
    editando: false
  },
  data () {
    return {
      currentPage: 0,
      pageCount: 0,
      form: {},
      redes: [],
      empresas: [],
      lojas: [],
      filePDF: null,
      flgDigitalizando: false,
      tipo: null,
      docProposta: null,
      docRgFrente: null,
      docRgVerso: null,
	  docCnh: null,
      docOutros: [],
      listDocIds: [],
      currentImage: null,
      dialogImage: false,
      dialogDelete: false,
      anexoId: null,
      loadingBtn: false,
      loading: false
    }
  },
  watch: {
    value () {
      this.$emit('input', this.value)
    }
  },
  beforeMount () {
    this.callbackDigitalizarDoc(true)
  },
  methods: {
    openDigitalizar (tipo) {
      this.tipo = tipo
      this.flgDigitalizando = true
    },
    validateBeforeSubmit () {
      if (!!this.docProposta && (!!this.docRgFrente && !!this.docRgVerso || !!this.docCnh)) {
        this.loadingBtn = true
        ClienteLojaBusiness.inserirClienteLoja({
          clienteId: this.value.id,
          lojaId: this.value.lojaId
        }).then(() => {
          this.loadingBtn = false
          this.nextStep()
        }).catch(erro => {
          this.$root.showErro(erro.data)
        })
      } else {
        this.$root.showAlerta(
          'É obrigatório add a proposta de adesão e um documento com foto!'
        )
      }
    },
    callbackDigitalizarDoc (reload) {
      // passar a flgDigitalizando para false
      this.flgDigitalizando = false
      this.tipo = null
      if (reload) {
        this.loading = true
        this.listDocIds = null
        this.docProposta = null
        this.docRgFrente = null
		this.docCnh = null
        this.docRgVerso = null
        this.docOutros = []
        // carregar os documentos já digitalizados e mostrar como miniaturas
        ClienteBusiness.getAnexosOfCliente(this.value.id, this.value.lojaId)
          .then(response => {
            this.listDocIds = response.data
            this.listDocIds.forEach(file => {
              switch (file.tipoDocumento) {
                case 'P':
                  this.docProposta = file
                  break
                case 'F':
                  this.docRgFrente = file
                  break
                case 'V':
                  this.docRgVerso = file
                  break
                case 'C':
                  this.docCnh = file
                  break
				default:
                  this.docOutros.push(file)
              }
            })
          })
          .catch(error => console.log('erro: ' + error))
          .finally(() => {
            this.loading = false
          })
      }
    },
    openDialog (image) {
      this.currentImage = image
      this.dialogImage = true
    },
    openDialogDelete (anexoId) {
      this.anexoId = anexoId
      this.dialogDelete = true
    },
    closeDialogDelete () {
      this.item = null
      this.dialogDelete = false
    },
    deleteItem () {
      this.loadingBtn = true
      ClienteBusiness.deleteAnexo(this.value.id, this.anexoId)
        .then(response => {
          this.anexoId = null
          this.dialogDelete = false
          this.$root.showSucesso('Registro excluido com sucesso')
          this.callbackDigitalizarDoc(true)
        })
        .catch(erro => this.$root.showError(erro.data))
        .finally(() => {
          this.loadingBtn = false
        })
    }
  }
}
</script>
